<template>
    <div>
        <v-card elevation="2" style="margin:0 auto" max-width="600" :loading="loading" :disabled="loading">
            <v-card-text>
                <div class="text-h5 text--primary">輸入UID和驗證碼</div>
                <v-sheet color="white" class="pa-3">
                    <v-form ref="form" v-model="valid" v-on:submit="login">
                        <v-text-field label="UID" v-model="uid" :rules="[v => !!v || '請填入UID']" required></v-text-field>
                        <v-text-field label="驗證碼" v-model="auth" :rules="[v => !!v || '請填入驗證碼']" required></v-text-field>
                        <v-checkbox v-model="know" label="我知道且我同意此網站記錄我的UID和抽取資訊" :rules="[v => !!v || '同意後才可以記錄']" required></v-checkbox>
                        <br>
                        <v-btn depressed block color="primary" @click="login" :loading="loading">登入</v-btn>
                    </v-form>
                </v-sheet>
                <div v-if="reveal">
                    <br><br><br><br><br>
                </div>
            </v-card-text>
            <v-expand-transition>
                <v-card v-if="reveal" class="transition-fast-in-fast-out v-card--reveal" style="height: 100%;">
                    <v-card-text class="pb-0">
                        <p class="text-h6 text--primary">
                            {{uid}}
                        </p>
                        <v-sheet class="d-flex flex-wrap">
                            <div v-for="img in choiceList" :key="img" class="pa-1">
                                <v-img :src="img_url(img)" contain max-width="70"></v-img>
                            </div>
                        </v-sheet>
                    </v-card-text>
                    <br>
                    <v-card-actions class="py-3 px-5">
                        <v-btn color="primary" @click="reveal = false" depressed block>
                            關閉
                        </v-btn>
                    </v-card-actions>
                </v-card>
            </v-expand-transition>
        </v-card>
        <v-snackbar v-model="snackbar.display">
            <v-chip class="px-3" :color="snackbar.status == 'success' ? 'green' : 'red'" text-color="white" v-if="snackbar.status">
                {{snackbar.status}}
            </v-chip>
            &nbsp;
            {{ snackbar.text }}
            <template v-slot:action="{ attrs }">
                <v-btn color="red" text v-bind="attrs" @click="snackbar.display = false">
                    <v-icon>mdi-close</v-icon>
                </v-btn>
            </template>
        </v-snackbar>
    </div>
</template>

<script>
import constants from '@/constants'

export default {
    name: 'HelloWorld',
    data(){
        return{
            uid: "",
            auth: "",
            loading: false,
            snackbar: {
                display: false,
                text: "test",
                status: null
            },
            valid: false,
            reveal: false,
            choiceList: [],
            know: true
        }
    },
    methods:{
        login(){
            if(!this.$refs.form.validate()) return
            this.loading = true
            this.snackbar.display = false
            this.$http.get(constants.TOS_LOGIN_URL + "?uid=" + this.uid + "&auth=" + this.auth + "&labels=%7B%22serviceType%22:%22tosGame%22,%22campaignId%22:%2212%22%7D&testquery=").then((login_res) => {
                this.$http.get(constants.TOS_TOKEN_URL + "?token=" + login_res.data.token + "&testquery=").then((res) => {
                    if(!res.data.userRecord.choiceList.length){
                        this.snackbar.text = "請先到官網抽取後再回來"
                        this.snackbar.status = res.data.success ? "success" : "error"
                        this.snackbar.display = true
                        this.loading = false
                        return
                    }
                    let param = "sheet_id=" + constants.SHEET_ID + "&uid=" + res.data.userRecord._id + "&time=" + new Date().toISOString() + "&draw=%23" + res.data.userRecord.choiceList.toString() + "&reward=" + res.data.userRecord.rewardValue
                    this.$http.get(constants.RECORD_URL + "?" + param).then((record_res) => {
                        this.choiceList = res.data.userRecord.choiceList
                        this.reveal = true
                        this.snackbar.text = record_res.data.message
                        this.snackbar.status = record_res.data.success ? "success" : "error"
                        this.snackbar.display = true
                        this.loading = false
                    })
                }).catch((err) => {
                    this.snackbar.text = err.response.data.message
                    this.snackbar.status = err.response.status
                    this.snackbar.display = true
                    this.loading = false
                })
            }).catch((err) => {
                this.snackbar.text = err.response.data.message
                this.snackbar.status = err.response.status
                this.snackbar.display = true
                this.loading = false
            })
        },
        img_url(id){
            return "https://web-assets.tosconfig.com/gallery/icons/" + id + ".jpg"
        }
    }

}
</script>

<style>
.v-card--reveal {
  bottom: 0;
  opacity: 1 !important;
  position: absolute;
  width: 100%;
}
</style>